<template>
    <div id="comOrdinary">
      <div id="comOrdinary-content">
        <div id="comOrdinaryClientID">
          <span><span class="spanname"></span>:</span>
          <el-select v-model="comOrdinaryClientName" placeholder="" @change="handleClickcomOrdinary">
            <el-option
              v-for="item in comOrdinaryOptions"
              :key="item.clientName"
              :value="item.clientName">
            </el-option>
          </el-select>
        </div>
        <!--  -->
        <div id="comOrdinarySymbol">
          <span>:</span>
          <el-input v-model="comOrdinarySymbol" maxlength="6" placeholder=""></el-input>          
        </div>
        <!--  -->
        <div id="comOrdinaryOrderQty">
          <span>:</span>
          <el-input v-model="comOrdinaryOrderQty" placeholder=""></el-input>
        </div>
        <!--  -->
        <div id="comOrdinaryPrice">
          <span>:</span>
          <el-input v-model="comOrdinaryPrice" placeholder=""></el-input>
        </div>
        <!--  -->
        <div id="comOrdinarySide">
          <span class="comOrdinarySideSpan1"></span><span class="comOrdinarySideSpan2">:</span>
          <el-radio v-model="comOrdinarySide" label=""></el-radio>
          <el-radio v-model="comOrdinarySide" label=""></el-radio>
        </div>
        <template>
          <el-button type="primary" id="sureOrder" @click="sureOrder"></el-button>
        </template>
      </div>
      <!--  -->
      <div id="comOrdinaryPayback">
        <el-table
          :data="comOrdinaryTableData"
					:span-method="comOrdinaryMethod"
          border
          style="width: 100%; margin-top: 20px">
          <el-table-column  prop="ordStatus"  label="	"></el-table-column>
          <el-table-column  prop="sendingTime"  label=""></el-table-column>
          <el-table-column  prop="clOrdId"  label=""></el-table-column>
          <el-table-column  prop="clientId"  label=""></el-table-column>
          <el-table-column  prop="handlInst"  label=""></el-table-column>
          <el-table-column  prop="symbol"  label=""></el-table-column>
          <el-table-column  prop="securityType"  label=""></el-table-column>
          <el-table-column  prop="securityExchange"  label=""></el-table-column>
          <el-table-column  prop="side"  label="/"></el-table-column>
          <el-table-column  prop="transactTime"  label=""></el-table-column>
          <el-table-column  prop="orderQty"  label=""></el-table-column>
          <el-table-column  prop="ordType"  label=""></el-table-column>
          <el-table-column  prop="price"  label=""></el-table-column>
          <el-table-column  prop="cumQty"  label=""></el-table-column>
          <el-table-column  prop="leavesQty"  label=""></el-table-column>
          <el-table-column  prop="avgPx"  label=""></el-table-column>
          <el-table-column fixed="right" width="100"  label="">
            <template slot-scope="scope">
              <!--  -->
              <el-button type="danger" @click="comOrdinaryUndo(scope.row)">{{txtsMsg}}</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
</template>

<script>
import axios from "axios";
import common from "@/utils/common"
export default {
	// reload
  inject:['reload'],
  name: "commonOrdinary",
  data() {
    return {
      txtsMsg:'',
      commonOrdinaryToken:'',
      commonOrdinaryUserID:common.decipher('wsUserID'),
      comOrdinaryOptions:[],
      comOrdinaryClientID:'',//ClientID
      comOrdinaryClientName:'',//ClientName
      comOrdinarySymbol:'',//
      comOrdinaryOrderQty:'',//
      comOrdinaryPrice:'',//
      comOrdinarySide:'',///
      comOrdinaryTableData:[],//
      websock:'',
      responseDatas:[],
    };
  },
  mounted:function(){
    // 
    this.comOrdinaryPro();
    // 
    this.getReturnActual();
    // websocket,
    this.websocketInit()
  },
  methods: {
		// 
		getReturnActual(){
      console.log(common.decipher('wsCustomerID'),common.decipher('wsUserID'),common.decipher('wsUserName'),common.decipher('wsUserRole'))
      this.commonOrdinaryUserID = common.decipher('wsUserID');
			console.log(domain.QueryOrderEightByUserId+this.commonOrdinaryUserID+'/'+'1'+'/'+'1'+'/'+'100')
			axios.get(domain.QueryOrderEightByUserId+this.commonOrdinaryUserID+'/'+'1'+'/'+'1'+'/'+'100')
				.then((response) => {
					console.log(response)
					// if (response.data.httpCode == '500') {
          //   common.altPrompt('')
					// 	this.comOrdinaryTableData = [];
          // } else 
          if (response.data.code == '1'){
            // for
						for (var i = 0; i < response.data.responseEntity.pageData.length; i++) {
              // 01246810
              if (response.data.responseEntity.pageData[i].ordStatus == '0') {
                response.data.responseEntity.pageData[i].ordStatus = ''
              } else if (response.data.responseEntity.pageData[i].ordStatus == '1') {
                response.data.responseEntity.pageData[i].ordStatus = ''
              } else if (response.data.responseEntity.pageData[i].ordStatus == '2') {
                response.data.responseEntity.pageData[i].ordStatus = ''
              } else if (response.data.responseEntity.pageData[i].ordStatus == '4') {
                response.data.responseEntity.pageData[i].ordStatus = ''
              } else if (response.data.responseEntity.pageData[i].ordStatus == '6') {
                response.data.responseEntity.pageData[i].ordStatus = ''
              } else if (response.data.responseEntity.pageData[i].ordStatus == '8') {
                response.data.responseEntity.pageData[i].ordStatus = ''
              } else if (response.data.responseEntity.pageData[i].ordStatus == '10') {
                response.data.responseEntity.pageData[i].ordStatus = ''
              }
              response.data.responseEntity.pageData[i].sendingTime = common.changeTime(response.data.responseEntity.pageData[i].sendingTime)
              response.data.responseEntity.pageData[i].transactTime = common.changeTime(response.data.responseEntity.pageData[i].transactTime)
              // ,0 CS,13 RB,15 ETF
              if(response.data.responseEntity.pageData[i].securityType == '0'){
                response.data.responseEntity.pageData[i].securityType = 'CS'
              }else if(response.data.responseEntity.pageData[i].securityType == '13'){
                response.data.responseEntity.pageData[i].securityType = 'RB'
              }else if(response.data.responseEntity.pageData[i].securityType == '15'){
                response.data.responseEntity.pageData[i].securityType = 'ETF'
              }
              // 12
              if(response.data.responseEntity.pageData[i].side == '1'){
                response.data.responseEntity.pageData[i].side = ''
              }else if(response.data.responseEntity.pageData[i].side == '2'){
                response.data.responseEntity.pageData[i].side = ''
              }
              // ,1SZ2SH
              if(response.data.responseEntity.pageData[i].securityExchange == '1'){
                response.data.responseEntity.pageData[i].securityExchange = 'SZ'
              }else if(response.data.responseEntity.pageData[i].securityExchange == '2'){
                response.data.responseEntity.pageData[i].securityExchange = 'SH'
              }
              // 01
              if(response.data.responseEntity.pageData[i].ordType == '0'){
                response.data.responseEntity.pageData[i].ordType = ''
              }else if(response.data.responseEntity.pageData[i].ordType == '1'){
                response.data.responseEntity.pageData[i].ordType = ''
              }
              // 00
              let a = '';
              a += response.data.responseEntity.pageData[i].symbol
              if (a.length == 0) {
                response.data.responseEntity.pageData[i].symbol = '000000'
              }else if (a.length == 1) {
                response.data.responseEntity.pageData[i].symbol = '00000'+a
              }else if(a.length == 2){
                response.data.responseEntity.pageData[i].symbol = '0000'+a
              }else if(a.length == 3){
                response.data.responseEntity.pageData[i].symbol = '000'+a
              }else if(a.length == 4){
                response.data.responseEntity.pageData[i].symbol = '00'+a
              }else if(a.length == 5){
                response.data.responseEntity.pageData[i].symbol = '0'+a
              }
						}
						this.comOrdinaryTableData = response.data.responseEntity.pageData;
					}
				})
				.catch((error) => {
					console.log(error)
        })
    },
    // websocket
		readyChat(data){
      // 1_clientId2_userId
      let that = this;
      console.log(data,this.websock.readyState)
      if (this.websock.readyState === 1) {//
        console.log('readyState === 1')
				this.websocketSend(data)
      } else if(this.websock.readyState === 0){//300ms
        console.log('')
				setTimeout(that.websocketSend(data),300)
			}else{//,500ms
        console.log('')
        this.websocketInit();
				setTimeout(that.websocketSend(data),500)
			}
		},
		// websocket
		websocketInit(){
      console.log(domain.authBaseWebsocketOrdinary)
      this.websock = new WebSocket(domain.authBaseWebsocketOrdinary);
      console.log(this.websock.readyState)
      // onopen
      this.websock.onopen = this.websocketOpen;
      // onmessage
			this.websock.onmessage = this.websocketMessage;
		},
    // websocketuserId,
    websocketOpen(data){
      console.log('userId_'+common.decipher('wsUserID'))
      this.websock.send('userId_'+common.decipher('wsUserID'))
    },
		// websocket
		websocketSend(data){
      console.log(data,'')
			this.websock.send(data)
    },
		// webscoket
		websocketMessage(e){
      console.log(e)
      this.responseDatas = JSON.parse(e.data)
      if (common.isNull(e)) {
        common.altPrompt('')
				this.comOrdinaryTableData = [];
      } else if(this.responseDatas.handlInst == '1'){
        // this.responseDatas = JSON.parse(e.data)
        console.log(this.responseDatas,this.responseDatas.clOrdId,this.responseDatas.ordStatus)
        // 01246810
        if (this.responseDatas.ordStatus == '0') {
          this.responseDatas.ordStatus = ''
        } else if (this.responseDatas.ordStatus == '1') {
          this.responseDatas.ordStatus = ''
        } else if (this.responseDatas.ordStatus == '2') {
          this.responseDatas.ordStatus = ''//
        } else if (this.responseDatas.ordStatus == '4') {
          this.responseDatas.ordStatus = ''//
        } else if (this.responseDatas.ordStatus == '6') {
          this.responseDatas.ordStatus = ''//
        } else if (this.responseDatas.ordStatus == '8') {
          this.responseDatas.ordStatus = ''
        } else if (this.responseDatas.ordStatus == '10') {
          this.responseDatas.ordStatus = ''
        }
        this.responseDatas.sendingTime = common.changeTime(this.responseDatas.sendingTime)
        this.responseDatas.transactTime = common.changeTime(this.responseDatas.transactTime)
        // ,0 CS,13 RB,15 ETF
        if(this.responseDatas.securityType == '0'){
          this.responseDatas.securityType = 'CS'
        }else if(this.responseDatas.securityType == '13'){
          this.responseDatas.securityType = 'RB'
        }else if(this.responseDatas.securityType == '15'){
          this.responseDatas.securityType = 'ETF'
        }
        // 12
        if(this.responseDatas.side == '1'){
          this.responseDatas.side = ''
        }else if(this.responseDatas.side == '2'){
          this.responseDatas.side = ''
        }
        // ,1SZ2SH
        if(this.responseDatas.securityExchange == '1'){
          this.responseDatas.securityExchange = 'SZ'
        }else if(this.responseDatas.securityExchange == '2'){
          this.responseDatas.securityExchange = 'SH'
        }
        // 01
        if(this.responseDatas.ordType == '0'){
          this.responseDatas.ordType = ''
        }else if(this.responseDatas.ordType == '1'){
          this.responseDatas.ordType = ''
        }
        // 0
        let a = '';
        a += this.responseDatas.symbol
        if (a.length == 0) {
          this.responseDatas.symbol = '000000'
        }else if (a.length == 1) {
          this.responseDatas.symbol = '00000'+a
        }else if(a.length == 2){
          this.responseDatas.symbol = '0000'+a
        }else if(a.length == 3){
          this.responseDatas.symbol = '000'+a
        }else if(a.length == 4){
          this.responseDatas.symbol = '00'+a
        }else if(a.length == 5){
          this.responseDatas.symbol = '0'+a
        }
        // 
        // this.comOrdinaryTableData.unshift(this.responseDatas)
        let clOrdIdRes = '0';
        
        for (let i = 0; i < this.comOrdinaryTableData.length; i++) {
          if (this.responseDatas.clOrdId == this.comOrdinaryTableData[i].clOrdId) {
            clOrdIdRes = '1';
            // this.comOrdinaryTableData.splice(0,i)
            this.comOrdinaryTableData[i].ordStatus = this.responseDatas.ordStatus
            this.comOrdinaryTableData[i].sendingTime = this.responseDatas.sendingTime
            this.comOrdinaryTableData[i].clOrdId = this.responseDatas.clOrdId
            this.comOrdinaryTableData[i].clientId = this.responseDatas.clientId
            this.comOrdinaryTableData[i].handlInst = this.responseDatas.handlInst
            this.comOrdinaryTableData[i].symbol = this.responseDatas.symbol
            this.comOrdinaryTableData[i].securityType = this.responseDatas.securityType
            this.comOrdinaryTableData[i].securityExchange = this.responseDatas.securityExchange
            this.comOrdinaryTableData[i].side = this.responseDatas.side
            this.comOrdinaryTableData[i].transactTime = this.responseDatas.transactTime
            this.comOrdinaryTableData[i].orderQty = this.responseDatas.orderQty
            this.comOrdinaryTableData[i].ordType = this.responseDatas.ordType
            this.comOrdinaryTableData[i].price = this.responseDatas.price
            this.comOrdinaryTableData[i].cumQty = this.responseDatas.cumQty
            this.comOrdinaryTableData[i].leavesQty = this.responseDatas.leavesQty
            this.comOrdinaryTableData[i].avgPx = this.responseDatas.avgPx
          }
        }
        if (clOrdIdRes == '0') {
          this.comOrdinaryTableData.unshift(this.responseDatas)
        }
      }
    },
    // websocket
    websockclose(e){
      console.log("close"+e)
    },
    // 
    comOrdinaryPro: function() {
        this.commonOrdinaryUserID = common.decipher('wsUserID');
        console.log(domain.indFindCustomer+this.commonOrdinaryUserID)
				axios
        .get(domain.indFindCustomer+this.commonOrdinaryUserID)
        .then((response) => {
          console.log(response)
          if (response.data.code == '1') {
            this.comOrdinaryOptions = response.data.responseEntity;
          } else {
            common.altPrompt('')
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 
		handleClickcomOrdinary(){
      // 
      this.comOrdinaryTableData = [];
      // forclientid
			for (var i = 0; i < this.comOrdinaryOptions.length; i++) {
				if(this.comOrdinaryClientName == this.comOrdinaryOptions[i].clientName){
					this.commonOrdinaryClientID = this.comOrdinaryOptions[i].clientId
				}
      }
			console.log(domain.QueryOrderEightByClientId+this.commonOrdinaryClientID+'/'+'1'+'/'+'1'+'/'+'100')
			axios.get(domain.QueryOrderEightByClientId+this.commonOrdinaryClientID+'/'+'1'+'/'+'1'+'/'+'100')
				.then((response) => {
					console.log(response,common.getNowFormatDate())
					// if (response.data.httpCode == '500') {
          //   common.altPrompt('')
					// 	this.comOrdinaryTableData = [];
          // } else 
          if (response.data.code == '1'){
            // for
						for (var k = 0; k < response.data.responseEntity.pageData.length; k++) {
              // 01246810
              if (response.data.responseEntity.pageData[k].ordStatus == '0') {
                response.data.responseEntity.pageData[k].ordStatus = ''
              } else if (response.data.responseEntity.pageData[k].ordStatus == '1') {
                response.data.responseEntity.pageData[k].ordStatus = ''
              } else if (response.data.responseEntity.pageData[k].ordStatus == '2') {
                response.data.responseEntity.pageData[k].ordStatus = ''//
              } else if (response.data.responseEntity.pageData[k].ordStatus == '4') {
                response.data.responseEntity.pageData[k].ordStatus = ''//
              } else if (response.data.responseEntity.pageData[k].ordStatus == '6') {
                response.data.responseEntity.pageData[k].ordStatus = ''//
              } else if (response.data.responseEntity.pageData[k].ordStatus == '8') {
                response.data.responseEntity.pageData[k].ordStatus = ''
              } else if (response.data.responseEntity.pageData[k].ordStatus == '10') {
                response.data.responseEntity.pageData[k].ordStatus = ''
              }

              response.data.responseEntity.pageData[k].sendingTime = common.changeTime(response.data.responseEntity.pageData[k].sendingTime)
              response.data.responseEntity.pageData[k].transactTime = common.changeTime(response.data.responseEntity.pageData[k].transactTime)
              // ,0 CS,13 RB,15 ETF
              if(response.data.responseEntity.pageData[k].securityType == '0'){
                response.data.responseEntity.pageData[k].securityType = 'CS'
              }else if(response.data.responseEntity.pageData[k].securityType == '13'){
                response.data.responseEntity.pageData[k].securityType = 'RB'
              }else if(response.data.responseEntity.pageData[k].securityType == '15'){
                response.data.responseEntity.pageData[k].securityType = 'ETF'
              }
              // 12
              if(response.data.responseEntity.pageData[k].side == '1'){
                response.data.responseEntity.pageData[k].side = ''
              }else if(response.data.responseEntity.pageData[k].side == '2'){
                response.data.responseEntity.pageData[k].side = ''
              }
              // ,1SZ2SH
              if(response.data.responseEntity.pageData[k].securityExchange == '1'){
                response.data.responseEntity.pageData[k].securityExchange = 'SZ'
              }else if(response.data.responseEntity.pageData[k].securityExchange == '2'){
                response.data.responseEntity.pageData[k].securityExchange = 'SH'
              }
              // 01
              if(response.data.responseEntity.pageData[k].ordType == '0'){
                response.data.responseEntity.pageData[k].ordType = ''
              }else if(response.data.responseEntity.pageData[k].ordType == '1'){
                response.data.responseEntity.pageData[k].ordType = ''
              }
              // 0
              let a = '';
              a += response.data.responseEntity.pageData[k].symbol
              if (a.length == 0) {
                response.data.responseEntity.pageData[k].symbol = '000000'
              }else if (a.length == 1) {
                response.data.responseEntity.pageData[k].symbol = '00000'+a
              }else if(a.length == 2){
                response.data.responseEntity.pageData[k].symbol = '0000'+a
              }else if(a.length == 3){
                response.data.responseEntity.pageData[k].symbol = '000'+a
              }else if(a.length == 4){
                response.data.responseEntity.pageData[k].symbol = '00'+a
              }else if(a.length == 5){
                response.data.responseEntity.pageData[k].symbol = '0'+a
              }
						}
            this.comOrdinaryTableData = response.data.responseEntity.pageData;
					}
					
				})
				.catch((error) => {
					console.log(error)
        })
		},
    // 
    sureOrder(){
      // forclientID
      for (let i = 0; i < this.comOrdinaryOptions.length; i++) {
        if (this.comOrdinaryClientName == this.comOrdinaryOptions[i].clientName) {
          this.comOrdinaryClientID = this.comOrdinaryOptions[i].clientId;
        }
      }
      
      // websocket
      this.readyChat('clientId_'+this.commonOrdinaryClientID)
      console.log(this.responseDatas,this.responseDatas.clOrdId)

			let comOrdinarySymbolReg = /^[0-9]\d*$/;//
			// let comOrdinaryPriceReg = /^[1-9]\d*\.\d*|0\.\d*[1-9]\d*$/;
			let comOrdinaryPriceReg = /^[0-9]+(.[0-9]{2})?$/;//
      let comOrdinarySymbolCD1 = this.comOrdinarySymbol.substr(0,1);
      let comOrdinarySymbolCD2 = this.comOrdinarySymbol.substr(0,2);
      console.log(comOrdinarySymbolCD1,comOrdinarySymbolCD2,this.comOrdinarySymbol,this.comOrdinaryOrderQty.substr(0,1))

      if(common.isNull(this.comOrdinaryClientName)){
        common.altPrompt('')
        return false;
      }

      if((this.comOrdinarySymbol.length == '6') && (comOrdinarySymbolReg.test(this.comOrdinarySymbol)) && ((comOrdinarySymbolCD2 == '00') || (comOrdinarySymbolCD2 == '30') || (comOrdinarySymbolCD2 == '15') || (comOrdinarySymbolCD2 == '13') || (comOrdinarySymbolCD1 == '2') || (comOrdinarySymbolCD1 == '6'))){
        console.log('')
      }else{
        common.altPrompt('');
        return false;
      }

      if(comOrdinarySymbolReg.test(this.comOrdinaryOrderQty) && this.comOrdinaryOrderQty.substr(0,1) != 0 && common.goAliquot(this.comOrdinaryOrderQty) != false){
        console.log('')
      }else{
        common.altPrompt('');
        return false;
      }

      if(common.isNull(this.comOrdinaryPrice) || this.comOrdinaryPrice == 0){
        common.altPrompt('');
        return false;
      }else if(comOrdinarySymbolReg.test(this.comOrdinaryPrice) && this.comOrdinaryPrice.substr(0,1) != 0 || comOrdinaryPriceReg.test(this.comOrdinaryPrice)){
        console.log('')
      }else{
        common.altPrompt('');
        return false;
      }

      if (common.isNull(this.comOrdinarySide)) {
        common.altPrompt('')
        return false;
      }
      if (this.comOrdinarySide == '') {
        this.comOrdinarySide = '1'
      } else if(this.comOrdinarySide == ''){
        this.comOrdinarySide = '2'
      }
      console.log(domain.comOrdinaryNewActualOrder,this.comOrdinaryClientID,this.comOrdinarySymbol,this.comOrdinarySide,this.comOrdinaryOrderQty,this.comOrdinaryPrice,)
      // 
      axios.post(domain.comOrdinaryNewActualOrder,{
        ClientID:this.comOrdinaryClientID,
        Symbol:this.comOrdinarySymbol,
        Side:this.comOrdinarySide,
        OrderQty:this.comOrdinaryOrderQty,
        Price:this.comOrdinaryPrice,
      }).then((response) => {
        console.log(response)
        if (response.data.code == '1') {
          this.$message({
          	type: 'success',
          	message: ''
          })
          // this.comOrdinaryClientName = '';
          this.comOrdinarySymbol = '';
          this.comOrdinarySide = '';
          this.comOrdinaryOrderQty = '';
          this.comOrdinaryPrice = '';
          // this.handleClickcomOrdinary();
        }else{
					common.altPrompt('')
					this.comOrdinarySymbol = '';
					this.comOrdinarySide = '';
					this.comOrdinaryOrderQty = '';
          this.comOrdinaryPrice = '';
          this.handleClickcomOrdinary();
				}
      }).catch((error) => {
        console.log(error)
      })
    },
    // 
    comOrdinaryUndo(row,index){
      console.log(row,this.comOrdinaryClientName,domain.comOrdinaryCancelActualOrder+row.clOrdId+'/'+row.clientId)
      // 01246810
      if (row.ordStatus == '') {
        console.log('')
      }else if(row.ordStatus == ''){
        console.log('')
      }else if(row.ordStatus == ''){
        common.altPrompt('')
        return false
      }else if(row.ordStatus == ''){
        common.altPrompt('')
        return false
      }else if(row.ordStatus == ''){
        console.log('')
      }else if(row.ordStatus == ''){
        common.altPrompt('')
        return false
      }else if(row.ordStatus == ''){
        common.altPrompt('')
        return false
      }
      // 
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          // 
          axios.get(domain.comOrdinaryCancelActualOrder+row.clOrdId+'/'+row.clientId)
            .then((response) => {
              console.log(response)
              // 
              if (response.data.code == '1') {
                common.altPrompt('')
                if (common.isNull(this.comOrdinaryClientName)) {
                  this.reload()
                } else {
                  this.handleClickcomOrdinary()
                }
              } else{
                common.altPrompt('')
              }
            })
            .catch((error) => {
              console.log(error)
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
			
    },
		// 
		comOrdinaryMethod({ row, column, rowIndex, columnIndex }) {
			// console.log(row,column,rowIndex,columnIndex)
		},
  }
};
</script>

