<template>
    <div>
        <h2></h2>
        <template>
            <el-table
                :data="personEdit"
                border
                style="width: 100%">
                <el-table-column
                fixed
                label=""
                width="150">
                    <template slot-scope="scope">
                        <span>{{scope.row.date}}</span>
                        <el-input v-model="input1" placeholder="" v-show="isInputShow"></el-input>
                    </template>
                </el-table-column>
                <el-table-column
                label="">
                    <template slot-scope="scope">
                        <span>{{scope.row.name}}</span>
                        <el-input v-model="input2" placeholder="" v-show="isInputShow"></el-input>
                    </template>
                </el-table-column>
                <el-table-column
                label="ip">
                    <template slot-scope="scope">
                        <span>{{scope.row.ip}}</span>
                        <el-input v-model="input3" placeholder="ip" v-show="isInputShow"></el-input>
                    </template>
                </el-table-column>
                <el-table-column
                label="">
                    <template slot-scope="scope">
                        <span>{{scope.row.phone}}</span>
                        <el-input v-model="input4" placeholder="" v-show="isInputShow"></el-input>
                    </template>
                </el-table-column>
                <el-table-column  fixed="right"  label="1"  width="100">
                    <template slot-scope="scope">
                        <el-button @click="handleEdit(scope.$index,scope.row)" type="text" size="small">{{edits}}</el-button>
                    </template>
                </el-table-column>
                <el-table-column  fixed="right"  label="2"  width="100">
                    <template slot-scope="scope">
                        <el-button @click="handleSaves(scope.$index,scope.row)" type="text" size="small"></el-button>
                    </template>
                </el-table-column>
                <!--  -->
                <el-table-column  fixed="right"  label="3"  width="100">
                    <template slot-scope="scope">
                        <el-button @click="changePsw" type="text" size="small"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </template>
        <div id="perCenDoc" style="display:none">
            <el-button @click="elbut1"></el-button>
            <el-button @click="elbut2"></el-button>
            <div>
                <!--  -->
                <div v-show="isShow1" id="uploadDoc">
                    <el-upload
                    class="upload-demo"
                    accept=".csv"
                    ref="upload"
                    action="https://jsonplaceholder.typicode.com/posts/"
                    multiple
                    :on-preview="handlePreview"
                    :on-remove="handleRemove"
                    :auto-upload="false">
                        <el-button slot="trigger" size="small" type="primary"></el-button>
                        <el-button style="margin-left: 10px;" size="small" type="primary" @click="submitUpload"></el-button>
                        <div slot="tip" class="el-upload__tip">csv500kb</div>
                    </el-upload>
                </div>
                <!--  -->
                <div v-show="isShow2" id="downloadDoc">
                    <template>
                        <!--  -->
                        <template>
                            <div>
                                <span class="choiceStrategy"></span>
                                <el-select v-model="value" placeholder="">
                                    <el-option
                                    v-for="item in optionsPer"
                                    :key="item.value"
                                    :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </template>
                        <!--  -->
                        <div class="block">
                            <span class="demonstration"></span>
                            <el-date-picker
                            v-model="valueTimeRange"
                            type="datetimerange"
                            :picker-options="pickerOptions"
                            range-separator=""
                            start-placeholder=""
                            end-placeholder=""
                            align="right"
                            :default-time="['00:00:00', '23:59:59']">
                            </el-date-picker>
                        </div>
                    </template>
                    <el-button type="primary" id="download" name="download" @click="butDown"></el-button>
                </div>
            </div>
        </div>
        <!--  -->
        <div id="personCenAlt" v-show="changePswShow">
          <div id="personCenAlt-content">
            <i class="el-icon-close" @click="cencelPerPswX"></i>
            <div>
              <span></span>
              <el-input type="password" v-model="personCenNowPsw" placeholder=""></el-input>
            </div>
            <div>
              <span></span>
              <el-input type="password" v-model="personCenNewPsw" placeholder=""></el-input>
            </div>
            <div>
              <span></span>
              <el-input type="password" v-model="personCenNewPswConfirm" placeholder=""></el-input>
            </div>
            <p>{{perpsonPswValue}}</p>
            <div>
              <el-button round @click="cancelPerPsw"></el-button>
              <el-button type="success" round @click="subPerPsw"></el-button>
            </div>
          </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import common from "@/utils/common";
    export default {
        name:'personCenter',
        data() {
            return {
                perCustomerID:'',
                perUserID:'',
                perUsername:'',
                edits:'',
                personEdit: [
                    {
                        date: '',
                        name: '',
                        province: '',
                        ip: '',
                        phone:'',
                        zip: ''
                    }, 
                ],
                isInputShow:false,//
                input1:'',
                input2:'',
                input3:'',
                input4:'',
                isShow1:false,//
                isShow2:false,//
                optionsPer: [{
                value: 'T0',
                }, {
                value: 'Algo',
                }],//select
                value: '',//select
                valueTimeRange:'',//
                perValueTimeStar:'',
                perValueTimeEnd:'',
                pickerOptions:{
                    shortcuts: [
                        {
                            text: '',
                            onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                            }
                        }, {
                            text: '',
                            onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                            }
                        }, {
                            text: '',
                            onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                            }
                        }
                    ]
                },
                changePswShow:false,//
                personCenNowPsw:'',//
                personCenNewPsw:'',//
                personCenNewPswConfirm:'',//
                perpsonPswValue:'',//
            }
        },
        mounted:function(){
            this.getNameperson()
        },
        methods: {
            getNameperson(){
                for (let i = 0; i < this.personEdit.length; i++) {
                    this.personEdit[i].name = common.decipher('wsUserName')
                    this.personEdit[i].phone = common.decipher('userPhone')
                }
            },
            // 
            handleEdit(index,row) {
                console.log(index,row,row.date);
                if (this.edits == '') {
                    this.edits = '';
                    this.input1 = '';
                    this.input2 = '';
                    this.input3 = '';
                    this.input4 = '';
                    this.isInputShow = true;
                } else if(this.edits == '') {
                    this.edits = '';
                    this.isInputShow = false;
                }
            },
            // 
            handleSaves(index,row) {
                if ((common.isNull(this.input1))&&(common.isNull(this.input2))&&(common.isNull(this.input3))&&(common.isNull(this.input4))||(this.isInputShow == false)) {
                    common.altPrompt('');
                    return false;
                }else{
                    // for
                    for (let i = 0; i < this.personEdit.length; i++) {
                        if (common.isNotNull(this.input1)) {
                            this.personEdit[i].date = this.input1;
                        }
                        if(common.isNotNull(this.input2)){
                            this.personEdit[i].name = this.input2;
                        }
                        if(common.isNotNull(this.input3)){
                            this.personEdit[i].ip = this.input3;
                        }
                        if(common.isNotNull(this.input4)){
                            this.personEdit[i].phone = this.input4;
                        }
                    }
                    common.altPrompt('')
                    this.edits = '';
                    this.isInputShow = false;
                }
                console.log(index,row)
            },
            // 
            changePsw(){
                console.log('')
                this.changePswShow = true;
            },
            // 
            cancelPerPsw(){
                console.log('')
                this.personCenNowPsw = '';
                this.personCenNewPsw = '';
                this.personCenNewPswConfirm = '';
                this.changePswShow = false;
                common.altPrompt('')
            },
            //
            subPerPsw(){
                this.perUserID = common.decipher('wsUserID');
                if (common.isNull(this.personCenNowPsw)) {
                    common.altPrompt('')
                    return false
                }
                if (common.isNull(this.personCenNewPsw) || common.isNull(this.personCenNewPswConfirm)) {
                    common.altPrompt('')
                    return false
                }else if(this.personCenNewPsw != this.personCenNewPswConfirm){
                    common.altPrompt('')
                    return false
                }else if(this.personCenNowPsw == this.personCenNewPsw){
                    common.altPrompt('')
                    return false
                }
                console.log('q'+domain.personUpdatePwd+this.perUserID+'/'+this.personCenNewPsw)
                axios.get(domain.personUpdatePwd+this.perUserID+'/'+this.personCenNewPsw)
                    .then((response) => {
                        console.log(response)
                        if (response.data.code == '1') {
                            common.altPrompt('')
                            this.$router.push({
                                name:'login'
                            })
                        }else{
                            common.altPrompt('')
                        }
                    })
                    .catch((error) => {
                        console.log(error)
                    })
            },
            // x
            cencelPerPswX(){
                this.personCenNowPsw = '';
                this.personCenNewPsw = '';
                this.personCenNewPswConfirm = '';
                this.changePswShow = false;
            },
            // 
            elbut1(){
                this.isShow1 = true;
                this.isShow2 = false;
            },
            // 
            submitUpload() {
                this.$refs.upload.submit();
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            // 
            elbut2(){
                this.isShow2 = true;
                this.isShow1 = false;
            },
            // 
            butDown(){
                console.log(this.valueTimeRange)
               // let downURL = "https://codeload.github.com/douban/douban-client/legacy.zip/master";
                // window.open(downURL)
                this.perUsername = common.decipher('wsUserName');
                this.perCustomerID = common.decipher('wsCustomerID');
                this.valueTimeRange = common.changeTimeStamp(this.valueTimeRange);
                this.perValueTimeStar = common.changeTime(this.valueTimeRange.valueTimeStar);
                this.perValueTimeEnd = common.changeTime(this.valueTimeRange.valueTimeEnd);
                console.log(domain.perActualCsvFile+'/'+this.perCustomerID+'/'+this.value+'/'+this.perValueTimeStar,'',this.perCustomerID,this.value,this.perValueTimeStar,this.perValueTimeEnd)
                axios.get(domain.perActualCsvFile+this.perCustomerID+'/'+this.value+'/'+this.perValueTimeStar)
                    .then((response) => {
                        console.log(response)
                    })
                    .catch((error) => {
                        console.log(error)
                    })
            },
        },
    }
</script>

<style>
    #uploadDoc,#downloadDoc{
        padding-top: 25px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .demonstration{
        margin: 0 3px 0 50px;
    }
    #download{
        margin: 20px 0 0 20px;
    }
    .choiceStrategy{
        margin-left: -185px;
    }
</style>



