<template>
    <div>
        <div id="content-index">
            <div id="content-choice">
                <div id="index-fom">
                    <div class="lab-buy">
                        <span class="sp-ProNmae1"></span><span class="sp-ProNmae2"></span>
                            <!--  -->
                        <el-select v-model="selected1" placeholder="" @change="changePro()">
                            <el-option
                            v-for="option1 in options1"
                            :key="option1.id"
                            :value="option1.clientName">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="lab-buy">
                        <span class="sp-txt"></span>
                        <el-select v-model="selected2" placeholder="">
                            <el-option
                            v-for="option2 in options2"
                            :key="option2"
                            :value="option2">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="lab-buy">
                        <span style="margin-right:10px;"></span>
                        <el-select v-model="inpOrderType" placeholder="">
                            <el-option
                            v-for="item in inpOrderTypeOptions"
                            :key="item.inpOrderTypes"
                            :value="item.inpOrderTypes">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="lab-buy">
                        <span class="sp-txt"></span>
                        <el-input v-model="inpSymbol" placeholder="" maxlength="6"></el-input>
                    </div>
                    <div class="lab-buy">
                        <span class="sp-txt"></span>
                        <el-input v-model="inpOrderQty" placeholder=""></el-input>
                    </div>
                    <div class="lab-buy">
                        <span class="sp-txt"></span>
                        <el-input v-model="inpPrice" placeholder=""></el-input>
                    </div>
                    <div class="lab-buy">
                        <span class="sp-txt"></span>
                        <el-radio v-model="pickedRadio1" label=""></el-radio>
                        <el-radio v-model="pickedRadio1" label=""></el-radio>
                    </div>
                    <div class="lab-buy">
                        
                        <el-radio v-model="pickedRadio2" label=""></el-radio>
                        <el-radio v-model="pickedRadio2" label=""></el-radio>
                    </div>
                    <div class="lab-buy">
                        <span></span>
                        <el-radio v-model="pickedRadio3" label=""></el-radio>
                        <el-radio v-model="pickedRadio3" label=""></el-radio>
                    </div>
                    <div>
                        <span></span>
                        <el-date-picker
                        v-model="valueTime"
                        type="datetimerange"
                        start-placeholder=""
                        end-placeholder=""
                        :default-time="['00:00:00','23:59:59']">
                        </el-date-picker>
                    </div>
                </div>
                <el-button type="primary" id="indexsubOrder" @click="entrustStock"></el-button>
            </div>
            <!--  -->
            <div id="indexPayback">
                <el-table
                :data="indexTableData"
                border
                style="width: 100%; margin-top: 20px">
                <el-table-column  prop="ordStatus"  label="	"></el-table-column>
                <el-table-column  prop="sendingTime"  label=""></el-table-column>
                <el-table-column  prop="clOrdId"  label=""></el-table-column>
                <el-table-column  prop="clientId"  label=""></el-table-column>
                <el-table-column  prop="handlInst"  label=""></el-table-column>
                <el-table-column  prop="symbol"  label=""></el-table-column>
                <el-table-column  prop="securityType"  label=""></el-table-column>
                <el-table-column  prop="securityExchange"  label=""></el-table-column>
                <el-table-column  prop="side"  label="/"></el-table-column>
                <el-table-column  prop="transactTime"  label=""></el-table-column>
                <el-table-column  prop="orderQty"  label=""></el-table-column>
                <el-table-column  prop="ordType"  label=""></el-table-column>
                <el-table-column  prop="price"  label=""></el-table-column>
                <el-table-column  prop="cumQty"  label=""></el-table-column>
                <el-table-column  prop="leavesQty"  label=""></el-table-column>
                <el-table-column  prop="avgPx"  label=""></el-table-column>
                <el-table-column fixed="right" width="100"  label="">
                    <template slot-scope="scope">
                    <el-button type="danger" @click="indexUndo(scope.row)"></el-button>
                    </template>
                </el-table-column>
                </el-table>
            </div>
            <!--  -->
            <div class="block">
              <span class="demonstration"></span>
              <el-pagination style="margin:auto"
                background
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-size="10"
                layout="prev, pager, next, jumper"
                :total="150">
              </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";
import common from "@/utils/common";
export default {
    // reload
  inject:['reload'],
  name: "index",
  data: function() {
    return {
      userid: "",
      selected1: "", //
      options1: [],
      indexClientID: "",
      id: "",
      selected2: "", //
      options2: [],
      options2AccountId:[],//accountId
      indexAccountId:'',//accountId
      inpSymbol: "", //
      inpOrderQty: "", //
      inpPrice: "", //
      pickedRadio1: "", //
      pickedRadio2: "", //
      pickedRadio3: "", //
      inpOrderTypeOptions:[{inpOrderTypes:'TWAP'},{inpOrderTypes:'VWAP'}],//
      inpOrderType: "", //
      valueTime: "", //
      valueTimeStar: "", //
      valueTimeEnd: "", //
      //   
      indexTableData: [],
      datas:'',//
      currentPage: 1,
      pages:'',//
    };
  },
  methods: {
    // 
    showPro: function() {
      this.userid = common.decipher('wsUserID');
      console.log(domain.indFindCustomer + this.userid);
      axios
        .get(domain.indFindCustomer + this.userid)
        .then(response => {
          console.log(response);
          if (response.data.code == '1') {
            this.options1 = response.data.responseEntity;
          } else {
            common.altPrompt('')
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 
    changePro: function() {
      // 
      this.indexTableData = [];
      console.log(this.selected1, this.options1);
      //  forclientID
      for (let i = 0; i < this.options1.length; i++) {
        if (this.selected1 == this.options1[i].clientName) {
          this.indexClientID = this.options1[i].clientId;
        }
      }
      console.log(domain.indFindStrategy + this.indexClientID);
      // 
      axios
        .get(domain.indFindStrategy + this.indexClientID)
        .then(res => {
          console.log(res);
          this.options2 = [];
          if (res.data.code == '1') {
            for (let i = 0; i < res.data.responseEntity.length; i++) {
              // console.log(res.data[i].strategy)
              if (res.data.responseEntity[i].strategyId == "2001") {
                res.data.responseEntity[i].strategyId = "T0";
              } else if (res.data.responseEntity[i].strategyId == "2002") {
                res.data.responseEntity[i].strategyId = "Algo";
              }
              this.options2.push(res.data.responseEntity[i].strategyId);
            }
            this.options2AccountId = res.data.responseEntity
          } else {
            common.altPrompt('')
          }
          console.log(this.options2);
        })
        .catch(err => {
          console.log(err);
        });
      // 
      console.log(domain.QueryOrderEightByClientId+this.indexClientID+'/'+'3'+'/'+'1'+'/'+'100')
      axios.get(domain.QueryOrderEightByClientId+this.indexClientID+'/'+'3'+'/'+'1'+'/'+'100')
        .then((response) => {
          console.log(response)
          if (response.data.code == '1') {
            for (var j = 0; j < response.data.responseEntity.pageData.length; j++) {
              // 01246810
              if (response.data.responseEntity.pageData[j].ordStatus == '0') {
                response.data.responseEntity.pageData[j].ordStatus = ''
              } else if (response.data.responseEntity.pageData[j].ordStatus == '1') {
                response.data.responseEntity.pageData[j].ordStatus = ''
              } else if (response.data.responseEntity.pageData[j].ordStatus == '2') {
                response.data.responseEntity.pageData[j].ordStatus = ''
              } else if (response.data.responseEntity.pageData[j].ordStatus == '4') {
                response.data.responseEntity.pageData[j].ordStatus = ''
              } else if (response.data.responseEntity.pageData[j].ordStatus == '6') {
                response.data.responseEntity.pageData[j].ordStatus = ''
              } else if (response.data.responseEntity.pageData[j].ordStatus == '8') {
                response.data.responseEntity.pageData[j].ordStatus = ''
              } else if (response.data.responseEntity.pageData[j].ordStatus == '10') {
                response.data.responseEntity.pageData[j].ordStatus = ''
              }
              response.data.responseEntity.pageData[j].sendingTime = common.changeTime(response.data.responseEntity.pageData[j].sendingTime)
              response.data.responseEntity.pageData[j].transactTime = common.changeTime(response.data.responseEntity.pageData[j].transactTime)
              // ,0 CS,13 RB,15 ETF
              if(response.data.responseEntity.pageData[j].securityType == '0'){
                response.data.responseEntity.pageData[j].securityType = 'CS'
              }else if(response.data.responseEntity.pageData[j].securityType == '13'){
                response.data.responseEntity.pageData[j].securityType = 'RB'
              }else if(response.data.responseEntity.pageData[j].securityType == '15'){
                response.data.responseEntity.pageData[j].securityType = 'ETF'
              }
              // 12
              if(response.data.responseEntity.pageData[j].side == '1'){
                response.data.responseEntity.pageData[j].side = ''
              }else if(response.data.responseEntity.pageData[j].side == '2'){
                response.data.responseEntity.pageData[j].side = ''
              }
              // ,1SZ2SH
              if(response.data.responseEntity.pageData[j].securityExchange == '1'){
                response.data.responseEntity.pageData[j].securityExchange = 'SZ'
              }else if(response.data.responseEntity.pageData[j].securityExchange == '2'){
                response.data.responseEntity.pageData[j].securityExchange = 'SH'
              }
              // 01
              if(response.data.responseEntity.pageData[j].ordType == '0'){
                response.data.responseEntity.pageData[j].ordType = ''
              }else if(response.data.responseEntity.pageData[j].ordType == '1'){
                response.data.responseEntity.pageData[j].ordType = ''
              }
              // 00
              let a = '';
              a += response.data.responseEntity.pageData[j].symbol
              if (a.length == 0) {
                response.data.responseEntity.pageData[j].symbol = '000000'
              }else if (a.length == 1) {
                response.data.responseEntity.pageData[j].symbol = '00000'+a
              }else if(a.length == 2){
                response.data.responseEntity.pageData[j].symbol = '0000'+a
              }else if(a.length == 3){
                response.data.responseEntity.pageData[j].symbol = '000'+a
              }else if(a.length == 4){
                response.data.responseEntity.pageData[j].symbol = '00'+a
              }else if(a.length == 5){
                response.data.responseEntity.pageData[j].symbol = '0'+a
              }
            }
            this.indexTableData = response.data.responseEntity.pageData
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    // websocket
		indexReadyChat(data){
      // 1_clientId2_userId
      let that = this;
      console.log(data,this.websock.readyState)
      if (this.websock.readyState === 1) {//
        console.log('readyState === 1')
				this.indexWebsocketSend(data)
      } else if(this.websock.readyState === 0){//300ms
        console.log('')
				setTimeout(that.indexWebsocketSend(data),300)
			}else{//,500ms
        console.log('')
        this.indexWebsocketInit();
				setTimeout(that.indexWebsocketSend(data),500)
			}
		},
		// websocket
		indexWebsocketInit(){
      console.log(domain.authBaseWebsocketOrdinary)
      this.websock = new WebSocket(domain.authBaseWebsocketOrdinary);
      console.log(this.websock.readyState)
      // onopen
      this.websock.onopen = this.indexWebsocketOpen;
      // onmessage
			this.websock.onmessage = this.indexWebsocketMessage;
		},
    // websocketuserId,
    indexWebsocketOpen(data){
      console.log('userId_'+common.decipher('wsUserID'))
      this.websock.send('userId_'+common.decipher('wsUserID'))
    },
		// websocket
		indexWebsocketSend(data){
      console.log(data,'')
			this.websock.send(data)
    },
    // webscoket
		indexWebsocketMessage(e){
      console.log(e)
      this.responseDatas = JSON.parse(e.data)
      if (common.isNull(e)) {
        common.altPrompt('')
				this.indexTableData = [];
      } else if(this.responseDatas.handlInst == '3'){
        // this.responseDatas = JSON.parse(e.data)
        console.log(this.responseDatas,this.responseDatas.clOrdId,this.responseDatas.ordStatus)
        // 01246810
        if (this.responseDatas.ordStatus == '0') {
          this.responseDatas.ordStatus = ''
        } else if (this.responseDatas.ordStatus == '1') {
          this.responseDatas.ordStatus = ''
        } else if (this.responseDatas.ordStatus == '2') {
          this.responseDatas.ordStatus = ''//
        } else if (this.responseDatas.ordStatus == '4') {
          this.responseDatas.ordStatus = ''//
        } else if (this.responseDatas.ordStatus == '6') {
          this.responseDatas.ordStatus = ''//
        } else if (this.responseDatas.ordStatus == '8') {
          this.responseDatas.ordStatus = ''
        } else if (this.responseDatas.ordStatus == '10') {
          this.responseDatas.ordStatus = ''
        }
        this.responseDatas.sendingTime = common.changeTime(this.responseDatas.sendingTime)
        this.responseDatas.transactTime = common.changeTime(this.responseDatas.transactTime)
        // ,0 CS,13 RB,15 ETF
        if(this.responseDatas.securityType == '0'){
          this.responseDatas.securityType = 'CS'
        }else if(this.responseDatas.securityType == '13'){
          this.responseDatas.securityType = 'RB'
        }else if(this.responseDatas.securityType == '15'){
          this.responseDatas.securityType = 'ETF'
        }
        // 12
        if(this.responseDatas.side == '1'){
          this.responseDatas.side = ''
        }else if(this.responseDatas.side == '2'){
          this.responseDatas.side = ''
        }
        // ,1SZ2SH
        if(this.responseDatas.securityExchange == '1'){
          this.responseDatas.securityExchange = 'SZ'
        }else if(this.responseDatas.securityExchange == '2'){
          this.responseDatas.securityExchange = 'SH'
        }
        // 01
        if(this.responseDatas.ordType == '0'){
          this.responseDatas.ordType = ''
        }else if(this.responseDatas.ordType == '1'){
          this.responseDatas.ordType = ''
        }
        // 0
        let a = '';
        a += this.responseDatas.symbol
        if (a.length == 0) {
          this.responseDatas.symbol = '000000'
        }else if (a.length == 1) {
          this.responseDatas.symbol = '00000'+a
        }else if(a.length == 2){
          this.responseDatas.symbol = '0000'+a
        }else if(a.length == 3){
          this.responseDatas.symbol = '000'+a
        }else if(a.length == 4){
          this.responseDatas.symbol = '00'+a
        }else if(a.length == 5){
          this.responseDatas.symbol = '0'+a
        }
        // 
        let indexclOrdIdRes = '0';
        console.log(this.indexTableData)
        for (let i = 0; i < this.indexTableData.length; i++) {
          if (this.responseDatas.clOrdId == this.indexTableData[i].clOrdId) {
            indexclOrdIdRes = '1';
            this.indexTableData[i].ordStatus = this.responseDatas.ordStatus
            this.indexTableData[i].sendingTime = this.responseDatas.sendingTime
            this.indexTableData[i].clOrdId = this.responseDatas.clOrdId
            this.indexTableData[i].clientId = this.responseDatas.clientId
            this.indexTableData[i].handlInst = this.responseDatas.handlInst
            this.indexTableData[i].symbol = this.responseDatas.symbol
            this.indexTableData[i].securityType = this.responseDatas.securityType
            this.indexTableData[i].securityExchange = this.responseDatas.securityExchange
            this.indexTableData[i].side = this.responseDatas.side
            this.indexTableData[i].transactTime = this.responseDatas.transactTime
            this.indexTableData[i].orderQty = this.responseDatas.orderQty
            this.indexTableData[i].ordType = this.responseDatas.ordType
            this.indexTableData[i].price = this.responseDatas.price
            this.indexTableData[i].cumQty = this.responseDatas.cumQty
            this.indexTableData[i].leavesQty = this.responseDatas.leavesQty
            this.indexTableData[i].avgPx = this.responseDatas.avgPx
          }
        }
        if (indexclOrdIdRes == '0') {
          this.indexTableData.unshift(this.responseDatas)
        }
      }
    },
    // websocket
    indexWebsockclose(e){
      console.log("close"+e)
    },
    // 
    entrustStock() {
      // 
      console.log(
        common.getNowFormatDate(),
        new Date(common.getNowFormatDate()).getTime(),
        this.options2AccountId,this.selected2,this.selected1, this.options1,
      );
      //  forclientID
      for (let i = 0; i < this.options1.length; i++) {
        if (this.selected1 == this.options1[i].clientName) {
          this.indexClientID = this.options1[i].clientId;
        }
      }
      // websocket
      console.log(this.indexClientID)
      this.indexReadyChat('clientId_'+this.indexClientID)
      
      // 
      this.valueTime = common.changeTimeStamp(this.valueTime);
      this.valueTimeStar = this.valueTime.valueTimeStar;
      this.valueTimeEnd = this.valueTime.valueTimeEnd;
      
      let choiceTime = new Date(this.valueTimeStar).getTime();
      let nowTime = new Date(common.getNowFormatDate()).getTime();
      console.log(this.valueTimeStar, this.valueTimeEnd, choiceTime, nowTime);
      let inpSymbolReg = /^[0-9]\d*$/; //
      // let inpSymbolPriceReg = /^[1-9]\d*\.\d*|0\.\d*[1-9]\d*$/;
      let inpSymbolPriceReg = /^[0-9]+(.[0-9]{2})?$/;//
      let inpSymbolCD1 = this.inpSymbol.substr(0, 1);
      let inpSymbolCD2 = this.inpSymbol.substr(0, 2);
      console.log(inpSymbolCD1, inpSymbolCD2, this.inpOrderQty.substr(0, 1));
      if (common.isNull(this.selected1)) {
        common.altPrompt("");
        return false;
      }
      if (common.isNull(this.selected2)) {
        common.altPrompt("");
        return false;
      }
      if (common.isNull(this.inpOrderType)) {
        common.altPrompt("");
        return false;
      }
      if (
        this.inpSymbol.length == "6" &&
        inpSymbolReg.test(this.inpSymbol) &&
        (inpSymbolCD2 == "00" ||
          inpSymbolCD2 == "30" ||
          inpSymbolCD2 == "15" ||
          inpSymbolCD2 == "13" ||
          inpSymbolCD1 == "2" ||
          inpSymbolCD1 == "5" ||
          inpSymbolCD1 == "6")
      ) {
        console.log("");
      } else {
        common.altPrompt("");
        return false;
      }
      if (
        inpSymbolReg.test(this.inpOrderQty) &&
        this.inpOrderQty.substr(0, 1) != 0 && 
        common.goAliquot(this.inpOrderQty) != false
      ) {
        console.log("");
      } else {
        common.altPrompt("");
        return false;
      }
      if (common.isNull(this.inpPrice) || this.inpPrice == 0) {
        common.altPrompt("");
        return false;
      } else if (inpSymbolReg.test(this.inpPrice) && this.inpPrice.substr(0, 1) != 0 || inpSymbolPriceReg.test(this.inpPrice)) {
        console.log("");
      }else {
        common.altPrompt("");
        return false;
      }
      if (common.isNull(this.pickedRadio1)) {
        common.altPrompt("");
        return false;
      }
      if (common.isNull(this.pickedRadio2)) {
        common.altPrompt("");
        return false;
      }
      if (common.isNull(this.pickedRadio3)) {
        common.altPrompt("");
        return false;
      }
      if (choiceTime < nowTime) {
        common.altPrompt("");
        this.valueTime = "";
        return false;
      }
      // 
      for (let i = 0; i < this.options2AccountId.length; i++) {
        if (this.selected2 == this.options2AccountId[i].strategyId) {
          this.indexAccountId = this.options2AccountId[i].accountId
        }
      }
      if (this.pickedRadio1 == '') {
        this.pickedRadio1 = '1'
      } else if (this.pickedRadio1 == '') {
        this.pickedRadio1 = '2'
      }
      if (this.pickedRadio2 == '') {
        this.pickedRadio2 = '1'
      } else if (this.pickedRadio2 == '') {
        this.pickedRadio2 = '0'
      }
      if (this.pickedRadio3 == '') {
        this.pickedRadio3 = '1'
      } else if (this.pickedRadio3 == '') {
        this.pickedRadio3 = '0'
      }
      // 
      console.log(domain.indNewAlgorithmOrder,this.pickedRadio1,this.pickedRadio2,this.pickedRadio3,this.indexAccountId);
      axios
        .post(domain.indNewAlgorithmOrder, {
          userid: this.userid,
          clientID: this.indexClientID, //
          AccountID: this.indexAccountId, //
          symbol: this.inpSymbol, //
          orderQty: this.inpOrderQty, //
          price: this.inpPrice, //
          side: this.pickedRadio1, //
          limitAction: this.pickedRadio2, //
          afterAction: this.pickedRadio3, //
          ordType: this.inpOrderType, //
          EffectiveTime: this.valueTimeStar, //
          ExpireTime: this.valueTimeEnd, //
        })
        .then(response => {
          console.log(response);
          if (response.data.code == "1") {
            common.altPrompt("");
            // this.selected1 = "";
            this.selected2 = "";
            this.inpSymbol = "";
            this.inpOrderQty = "";
            this.inpPrice = "";
            this.pickedRadio1 = "";
            this.pickedRadio2 = "";
            this.pickedRadio3 = "";
            this.inpOrderType = "";
            this.valueTimeStar = "";
            this.valueTimeEnd = "";
            // this.reload();
            this.changePro();
          } else{
            common.altPrompt("");
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 
    indexUndo(row, index) {
      console.log(row, index);
    },
    // 
    getSimulateReturns(){
      // handlInst =3:1,pageNo 1,pageSize 100
      this.userid = common.decipher('wsUserID')
      console.log(domain.QueryOrderEightByUserId+this.userid+'/'+'3'+'/'+'1'+'/'+'100')
      axios.get(domain.QueryOrderEightByUserId+this.userid+'/'+'3'+'/'+'1'+'/'+'100')
        .then((response) => {
          console.log(response)
          if (response.data.code == '1') {
            for (var i = 0; i < response.data.responseEntity.pageData.length; i++) {
              // 01246810
              if (response.data.responseEntity.pageData[i].ordStatus == '0') {
                response.data.responseEntity.pageData[i].ordStatus = ''
              } else if (response.data.responseEntity.pageData[i].ordStatus == '1') {
                response.data.responseEntity.pageData[i].ordStatus = ''
              } else if (response.data.responseEntity.pageData[i].ordStatus == '2') {
                response.data.responseEntity.pageData[i].ordStatus = ''
              } else if (response.data.responseEntity.pageData[i].ordStatus == '4') {
                response.data.responseEntity.pageData[i].ordStatus = ''
              } else if (response.data.responseEntity.pageData[i].ordStatus == '6') {
                response.data.responseEntity.pageData[i].ordStatus = ''
              } else if (response.data.responseEntity.pageData[i].ordStatus == '8') {
                response.data.responseEntity.pageData[i].ordStatus = ''
              } else if (response.data.responseEntity.pageData[i].ordStatus == '10') {
                response.data.responseEntity.pageData[i].ordStatus = ''
              }
              response.data.responseEntity.pageData[i].sendingTime = common.changeTime(response.data.responseEntity.pageData[i].sendingTime)
              response.data.responseEntity.pageData[i].transactTime = common.changeTime(response.data.responseEntity.pageData[i].transactTime)
              // ,0 CS,13 RB,15 ETF
              if(response.data.responseEntity.pageData[i].securityType == '0'){
                response.data.responseEntity.pageData[i].securityType = 'CS'
              }else if(response.data.responseEntity.pageData[i].securityType == '13'){
                response.data.responseEntity.pageData[i].securityType = 'RB'
              }else if(response.data.responseEntity.pageData[i].securityType == '15'){
                response.data.responseEntity.pageData[i].securityType = 'ETF'
              }
              // 12
              if(response.data.responseEntity.pageData[i].side == '1'){
                response.data.responseEntity.pageData[i].side = ''
              }else if(response.data.responseEntity.pageData[i].side == '2'){
                response.data.responseEntity.pageData[i].side = ''
              }
              // ,1SZ2SH
              if(response.data.responseEntity.pageData[i].securityExchange == '1'){
                response.data.responseEntity.pageData[i].securityExchange = 'SZ'
              }else if(response.data.responseEntity.pageData[i].securityExchange == '2'){
                response.data.responseEntity.pageData[i].securityExchange = 'SH'
              }
              // 01
              if(response.data.responseEntity.pageData[i].ordType == '0'){
                response.data.responseEntity.pageData[i].ordType = ''
              }else if(response.data.responseEntity.pageData[i].ordType == '1'){
                response.data.responseEntity.pageData[i].ordType = ''
              }
              // 00
              let a = '';
              a += response.data.responseEntity.pageData[i].symbol
              if (a.length == 0) {
                response.data.responseEntity.pageData[i].symbol = '000000'
              }else if (a.length == 1) {
                response.data.responseEntity.pageData[i].symbol = '00000'+a
              }else if(a.length == 2){
                response.data.responseEntity.pageData[i].symbol = '0000'+a
              }else if(a.length == 3){
                response.data.responseEntity.pageData[i].symbol = '000'+a
              }else if(a.length == 4){
                response.data.responseEntity.pageData[i].symbol = '00'+a
              }else if(a.length == 5){
                response.data.responseEntity.pageData[i].symbol = '0'+a
              }
            }
            this.indexTableData = response.data.responseEntity.pageData
          }
        })
        .catch((error) => {
